  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {
    //進捗画面
    setIsLoading(true);
    //情報のロード
    formatLstData();

  }, []);    //[] 忘れないよう…　初期化

  //APIJSON　→　画面用JSON（FLGやERRMSGなど）
  // const formatLstData = async () => {
  const formatLstData = () => {

    var url = BASE_URL + "/api/terminals_list.json";
    var data = {
    };

    Service(url, data, "GET").then(resp => {
      console.log(resp);
      // 正常系
      if (resp.status === 0) {
        var info = resp.infos;
        //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
        setPageInfos([info.count, info.pageSize, info.pageNo]);
        //Data情報の処理
        var lst = info.dataRows;
        lst.map(item => {
          return {
            ...item,
            //追加属性
            isEdit: false,
            chkResultName: false,
          }
        });
        setLstContent(lst);
      } else {
        //その他APIエラー      
      }
      setIsLoading(false);
    }).catch(resp => {
      //接続エラーなど
      setIsLoading(false);
      setMsgInfo([true, resp, 'danger']);
    });
  };

  //改ページ処理
  // pageInfos   : [記録数、ページサイズ,当ページ番号]
  const handleChangePageStart = (page) => {

    if (page.disabled) return;
    if (page.text === pageInfos[2]) return;

    setIsLoading(true);

    setTimeout(() => {

      if (page.text === 'Prev') {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] - 1]);
      } else if (page.text === 'Next') {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] + 1]);
      } else {
        setPageInfos([pageInfos[0], pageInfos[1], page.text]);
      }

      setIsLoading(false);

    }, 500);
  }

  const handleClickOpen = scrollType => () => {
    setOpen(true);
    setScroll(scrollType);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleSimple = event => {
    setSimpleSelect(event.target.value);
  };





  const handleClickEditDialogOpen = () => {
    setEditDialogOpen(true);
  };
  const handleClickEditDialogClose = () => {
    setEditDialogOpen(false);
  };



  const handleClickPlayListOpen = scrollType => () => {
    setPlayListOpen(true);
    setScroll(scrollType);
  };

  const handlePlayListClose = () => {
    setPlayListOpen(false);
  };

  
  //設置追加ボタン押す
